import {
    FETCH_DATA_PENDING,
    FETCH_DATA_SUCCESS,
    FETCH_DATA_FAILURE,
    FETCH_FILTER_DATA_PENDING,
    FETCH_FILTER_DATA_SUCCESS,
    FETCH_FILTER_DATA_FAILURE
} from "../constants/index";

export const getLaunchProgramListData = () => {
    return (dispatch) => {
        dispatch({ type: FETCH_DATA_PENDING });

        fetch(`https://api.spaceXdata.com/v3/launches?limit=100`)
            .then((res) => {
                return res.json();
            })
            .then((jsonResp) => {
                dispatch({ type: FETCH_DATA_SUCCESS, payload: jsonResp });
            })
            .catch((err) => {
                dispatch({ type: FETCH_DATA_FAILURE, payload: err });
            });
    };
}

export const getLaunchProgramFiltersData = ({
    launchYear,
    launchSuccess = null,
    launchLandingSuccess = null
}) => {
    console.log("in getLaunchProgramFiltersData ", launchYear, launchSuccess,
        launchLandingSuccess);

    return (dispatch) => {
        let url = `https://api.spaceXdata.com/v3/launches?limit=100`;

        if (launchYear) {
            url += `&launch_year=${launchYear}`;
        }

        if (launchSuccess != null) {
            url += `&launch_success=${launchSuccess}`;
        }

        if (launchLandingSuccess != null) {
            url += `&land_success=${launchLandingSuccess}`;
        }

        dispatch({ type: FETCH_FILTER_DATA_PENDING });

        fetch(url)
            .then((res) => {
                return res.json();
            })
            .then((jsonResp) => {
                dispatch({ type: FETCH_FILTER_DATA_SUCCESS, payload: jsonResp });
            })
            .catch((err) => {
                dispatch({ type: FETCH_FILTER_DATA_FAILURE, payload: err });
            });
    };
}