import React, { useState, useEffect } from 'react';
import { connect } from "react-redux";
import FilterButton from '../components/FilterButton';
import { getLaunchProgramFiltersData } from "../redux/actions";
import { getLaunchYears } from '../utils/launchYearUtil';

const FiltersList = (props) => {
    const { getLaunchFilterDataAction } = props;

    const yearsArr = getLaunchYears();
    const boolValues = ["true", "false"];

    const [selectedLaunchYear, setSelectedLaunchYear] = useState(null);
    const [selectedLaunchSuccessBool, setSelectedLaunchSuccessBool] = useState(null);
    const [selectedLandingSuccessBool, setSelectedLandingSuccessBool] = useState(null);

    const launchYearClickHandler = (item) => {
        selectedLaunchYear === item ?
            setSelectedLaunchYear(null) :
            setSelectedLaunchYear(item);

        callGetLaunchFilterDataAction();
    }

    const successfulLandingBtnClickHandler = (item) => {
        selectedLaunchSuccessBool === item ?
            setSelectedLaunchSuccessBool(null) :
            setSelectedLaunchSuccessBool(item);

        callGetLaunchFilterDataAction();
    }

    const successfulLaunchBtnClickHandler = (item) => {
        selectedLandingSuccessBool === item ?
            setSelectedLandingSuccessBool(null) :
            setSelectedLandingSuccessBool(item);

        callGetLaunchFilterDataAction();
    }

    const callGetLaunchFilterDataAction = () => {
        getLaunchFilterDataAction({
            launchYear: selectedLaunchYear,
            launchSuccess: selectedLaunchSuccessBool,
            launchLandingSuccess: selectedLandingSuccessBool
        });
    }

    return (
        <div id="filters_container">
            <b style={{ marginBottom: "20px" }}>Filters</b>

            <div className="sub_filters_container">
                <FilterButton
                    filterTitle="Launch Year"
                    data={yearsArr}
                    btnClick={launchYearClickHandler}
                    selectedFilter={selectedLaunchYear}
                />
            </div>

            <div className="sub_filters_container">
                <FilterButton
                    data={boolValues}
                    filterTitle="Successful Launch"
                    btnClick={successfulLaunchBtnClickHandler}
                    selectedFilter={selectedLaunchSuccessBool}
                />
            </div>

            <div className="sub_filters_container">
                <FilterButton
                    data={boolValues}
                    filterTitle="Successful landing"
                    btnClick={successfulLandingBtnClickHandler}
                    selectedFilter={selectedLandingSuccessBool}
                />
            </div>
        </div>
    )
}

const mapDispatchToProps = (dispatch) => {
    return {
        getLaunchFilterDataAction: (obj) => {
            console.log("obj ", obj);
            dispatch(getLaunchProgramFiltersData(obj));
        },
    };
}

export default connect(null, mapDispatchToProps)(FiltersList)
