import React, { Fragment } from 'react';
import { Col, Row, Card } from 'react-bootstrap';

const LaunchProgram = (props) => {

	const {
		launch_date_unix,
		flight_number,
		rocket: { rocket_id, rocket_name, first_stage },
		links: { mission_patch_small },
		mission_id,
		launch_year,
		launch_success,
	} = props.program;

	return (
		<Fragment>
			<Col
				key={`${launch_date_unix}_${flight_number}_${rocket_id}`}
				xs={12}
				sm={12}
				md={12}
				lg={12}
				style={{ marginBottom: "20px" }}
			>
				<Card>
					<div className="card_img_container">
						<Card.Img fluid="true" variant="top" src={mission_patch_small} />
					</div>
					<Card.Body>
						<Card.Title>{rocket_name}</Card.Title>
						<Card.Text>
							<b>Mission Ids: </b>
							{
								mission_id.length ?
									<ul>
										{mission_id.map((program, idx) => {
											return <li key={`${program}_${idx}`}>{program}</li>;
										})}
									</ul>
									:
									<span> - </span>
							}
						</Card.Text>

						<Card.Text>
							<b>Launch Year: </b>
							{launch_year}
						</Card.Text>

						<Card.Text>
							<b>Successful Launch: </b>
							{launch_success ? "true" : "false"}
						</Card.Text>

						<Card.Text>
							<b>Successful Landing: </b>
							{
								first_stage.cores &&
									first_stage.cores[0].land_success
									? "Yes"
									: "No"
							}
						</Card.Text>
					</Card.Body>
				</Card>
			</Col>
		</Fragment >
	)
}

export default LaunchProgram
