import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import Button from 'react-bootstrap/Button';

const FilterButton = ({ data, filterTitle, btnClick, selectedFilter }) => {

    return (
        <Fragment>
            <div id="filters_list">
                <h6 className="filters_title">{filterTitle}</h6>
                <hr className="filters_hr" />

                {
                    data.map((item) => (
                        <Button
                            key={Math.random()}
                            className="filters_btns"
                            active={
                                item === selectedFilter ?
                                    true : false
                            }
                            size="sm"
                            variant="success"
                            onClick={() => btnClick(item)}>
                            {item}
                        </Button>
                    ))
                }
            </div>
        </Fragment>
    )
}

