import React, { Fragment } from 'react';
import { Row, Col, Spinner } from 'react-bootstrap';
import { connect } from 'react-redux';
import LaunchProgram from '../components/LaunchProgram';
import ScrollToTop from "react-scroll-to-top";
import PageNotFound from '../Exception/404/404';
import Loader from './Loader/Loader';

const LaunchProgramList = (props) => {
    const { launchPrograms, showLoader } = props.getLaunchProgramList;

    return (
        <Fragment>
            <ScrollToTop smooth />
            <Row>
                {showLoader ?
                    (
                        <Col xs={12} sm={12} md={12} lg={12} style={{ textAlign: "center" }}>
                            <span>
                                <b>Preparing for launch </b>
                            </span>
                            <Spinner animation="border" variant="dark" />
                            {/* <Loader /> */}
                        </Col>
                    )
                    :
                    launchPrograms && launchPrograms.length ?
                        (
                            launchPrograms.map((program, idx) => {
                                return (
                                    <Col xs={12} sm={3} md={3} lg={3}
                                        style={{ padding: "0px", borderRadius: "5px" }}
                                        key={Math.random()}
                                    >
                                        <LaunchProgram program={program} />
                                    </Col>
                                );
                            })
                        ) : (
                            <Col xs={12} sm={12} md={12} lg={12}>
                                <PageNotFound />
                            </Col>
                        )}
            </Row>
        </Fragment >
    )
}

const mapStateToProps = (store) => {
    return {
        getLaunchProgramList: store.getLaunchProgramData,
    }
}

export default connect(mapStateToProps, null)(LaunchProgramList)
