import {
    FETCH_DATA_PENDING,
    FETCH_DATA_SUCCESS,
    FETCH_DATA_FAILURE,
    FETCH_FILTER_DATA_PENDING,
    FETCH_FILTER_DATA_SUCCESS,
    FETCH_FILTER_DATA_FAILURE
} from "../constants";
import { combineReducers } from "redux";

const initialState = {
    launchPrograms: [],
    error: '',
    isPending: false,
    showLoader: true,
};

const getLaunchProgramData = (state = initialState, action = {}) => {
    switch (action.type) {
        case FETCH_DATA_PENDING:
            return {
                ...state,
                isPending: true,
                showLoader: true
            };
        case FETCH_DATA_SUCCESS:
            return {
                ...state,
                launchPrograms: action.payload,
                isPending: false,
                showLoader: false,
            };
        case FETCH_DATA_FAILURE:
            return {
                ...state,
                isPending: false,
                error: action.payload,
                showLoader: false,
            };
        case FETCH_FILTER_DATA_PENDING:
            return {
                ...state,
                launchPrograms: [],
                isPending: true,
                showLoader: true
            };
        case FETCH_FILTER_DATA_SUCCESS:
            return {
                ...state,
                launchPrograms: action.payload,
                isPending: false,
                showLoader: false,
            };
        case FETCH_FILTER_DATA_FAILURE:
            return {
                ...state,
                launchPrograms: [],
                isPending: false,
                error: action.payload,
                showLoader: false,
            };
        default:
            return state;
    }
}

const launchProgramReducer = combineReducers({ getLaunchProgramData });

export default launchProgramReducer;

